<template>
  <div class="property">
    <div class="button from-file">
      <span>New from file</span>
      <input
        type="file"
        id="texture"
        name="files[]"
        ref="texture"
        v-on:change="$s.texturesManager.textureLoading($event)"
      />
    </div>
    <div class="button colorpicker">
      <span>New from color</span>
      <input type="color" 
        value="#e66465"
        @change="colorSelected"
      >
    </div>
  </div>
</template>

<script>
import $s from '@/components/store'

export default {
  computed: {
    manager: () => $s.texturesManager
  },

  methods: {
    colorSelected(e) {
      this.manager.createColorTexture(e.target.value)
    },
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/vars.scss';
  .button {
    position: relative;
    padding: 8px;
    box-sizing: border-box;
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    cursor: pointer;
    text-align: center;
    color: white;
    cursor: pointer;
    input {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      opacity: 0;
      color: white;
      cursor: pointer;
    }
  }
</style>


