<script>
import ObjectProps from './object';

export default {
  props: ['light'],

  methods: {
    showTexturePopup() {
      this.$refs['texture-popup'].show(this.light);
    }
  },

  components: { ObjectProps },

  computed: {
    obj() { return this.light.object },
    lightPos() { }
  },
}
</script>

<template>
  <div class="light-props" v-if="light">
    <label class="title">Light properties</label>

    <table>
      <!-- <tr class="property">
        <td class="name">
          Name
        </td>
        <td class="value">
          <input class="input" type="text" v-model="light.name" @input="$emit('name-changed')" />
        </td>
      </tr> -->

      <tr class="property">
        <td class="name big">Position</td>
        <td class="value">
          <div class="row">
            <label>X</label>
            <input class="input" type="text" v-model="light.position.x" />
          </div>
          <div class="row">
            <label>Y</label>
            <input class="input" type="text" v-model="light.position.y" />
          </div>
          <div class="row">
            <label>Z</label>
            <input class="input" type="text" v-model="light.position.z" />
          </div>
        </td>
      </tr>

      <tr class="property">
        <td class="name">
          Range
        </td>
        <td class="value">
          <input class="input" type="text" v-model="light.range" />
        </td>
      </tr>

      <tr class="property">
        <td class="name">
          Turn on
        </td>
        <td class="value checkbox">
          <input type="checkbox" v-model="light._on" />
        </td>
      </tr>
    </table>

    <ObjectProps :object="light.object" />
  </div>
</template>

<style lang="scss" scoped>
  @import '@/assets/vars.scss';
  .light-props {
    .title {
      color: white;
      text-align: center;
    }
    table {
      width: 100%;
      border-collapse: separate;
      border-spacing:0 8px; 
    }
    .property {
      background: $card-background;
      margin-top: 8px;
      padding: 8px;
      width: 100%;
      .name {
        color: white;
        padding: 8px;
        &.big {
          padding-top: 16px;
          vertical-align: top;
        }
      }
      .value {
        display: table;
        width: 100%;
        box-sizing: border-box;
        padding-right: 8px;
        .input {
          &[data-unit] {
            display: table;
            height: auto;
            border-collapse: separate;
            border-spacing: 0 0px;
            &::after {
              content: attr(data-unit);
              display: table-cell;
              padding: 0 8px;
              color: rgba($color: #fff, $alpha: 0.2)
            }
          }
        }
        input {
          width: 100%;
          background: none;
          padding: 8px 12px;
          border: none;
          background: rgba($color: #000000, $alpha: 0.2);
          width: 100%;
          box-sizing: border-box;
          color: white;
        }
        .row {
          display: table-row;
          & > * {
            display: table-cell;
          }
          label {
            color: white;
            width: 24px;
            height: 24px;
            line-height: 24px;
            text-align: center;
            background: rgba($color: #000000, $alpha: 0.4);
          }
          .input {
            position: relative;
            .unit {
              position: absolute;
              right: 0;
              top: 50%;
              transform: translateY(-50%);
              display: table-cell;
              color: rgba($color: #FFF, $alpha: .5);
            }
          }
        }
        button {
          padding: 8px;
          width: 100%;
          background: rgba($color: #000000, $alpha: 0.2);
        }
      }
    }
  }
</style>


