import * as Bronze from '@m0ksem/bronze'
import Sphere from '../../assets/sphere.obj'
import Vue from 'vue';

class LightManager {
  constructor (engine, store) {
    this.engine = engine
    this.store = store
    this.addLight();
  }

  addLight() {
    let light = Vue.observable(new Bronze.Light(this.engine))
        light.setPosition(0, 1500, 0)
        light.range = 1500
        light.on()

    let name = 'light'
    let index = 1;
    const sortedLights = this.engine.lights.sort((a, b) => a.name > b.name);
    sortedLights.forEach((current) => {
      if (current.name == name) {
         name += ' ' + index;
      }
      if (current.name == name + ' ' + index) {
        index++;
      }
    });
    light.name = name + ' ' + index;

    let object = new Bronze.Object(this.engine)
    object.position = light.position
    object.name = light.name
    object.compile(Sphere)
    object.type = 'light'
    object.selectable = true
    object.verticalAlign = true
    object.scale(20, 20, 20)
    object.useShader(this.engine.shaders["no-light"])
    object.light = light;
    light.object = object
  }

  removeLight(light) {
    if (this.engine.lights.lenght === 1) {
      return false;
    }
    this.engine.removeObject(light.object)
    const index = this.engine.lights.indexOf(light);
    this.engine.lights.splice(index, 1);
  }
}

export default LightManager



