import * as Bronze from '@m0ksem/bronze'
import { Rect, Grid } from '@m0ksem/bronze'
import KittenObj from '@/assets/kitten.obj'
import Sphere from '../../assets/sphere.obj'

class ObjectManager {
  constructor (engine, store) {
    this.engine = engine
    this.store = store
    const example = this.addKExample()
    example.setPosition(1000, 1000, -200)
    this.createGrid()
  }

  get objects() {
    return this.engine.objects // filter((obj) => obj.type !== 'light')
  }

  createRect() {
    const rect = new Rect(this.engine);
    rect.scale(10, 10, 10)
    rect.type = 'object'
    rect.selectable = true
  }

  createSkybox() {
    const skybox = new Bronze.Skybox(this.engine)
    skybox.type = 'object'
  }

  createSphere() {
    let object = new Bronze.Object(this.engine)
    object.setPosition(0, 0, 0)
    object.name = 'Just sphere :)'
    object.compile(Sphere)
    object.type = 'object'
    object.selectable = true
    object.verticalAlign = true
    object.scale(20, 20, 20)
  }

  createGrid() {
    let grid = new Grid(this.engine)
    grid.setTexture(this.store.texturesManager.defaultTextures.gridTexture)
    grid.setCellSize(100, 100)
    grid.setSize(50000, 50000)
    grid.setPosition(0, -5, 0)
    grid.setRotation(-90, 0, 0)
    grid.selectable = true
    grid.type = 'object'
  }

  addKExample() {
    const example = this.addObject(KittenObj, 'Kitten');
    example.scale(200, 200, 200);
    return example
  }

  addObject(objectText, filePath, updaingStatus) {
    let object = new Bronze.Object(this.engine)
    object.setPosition(0, 0, 0)
    object.compile(objectText, updaingStatus)
    object.filePath = filePath
    object.selectable = true
    object.verticalAlign = false
    object.type = 'object'

    let name = filePath.replace('.obj', '')
    name = name.charAt(0).toUpperCase() + name.slice(1)
    let index = 1
    this.engine.objects.forEach((o, i) => {
      if (!(o === object) && (o.name === name)) {
        let objectsCount = parseInt(o.name.charAt(name.length - 1))
        if (isNaN(objectsCount)) {
          objectsCount = 1
        }
        o.name = o.name + (objectsCount)
        name = name + (objectsCount + 1)
      }
      if (!(o === object) && (o.name === name + index)) {
        index += 1
      }
      if (!(o === object) && (o.name === name + 1)) {
        index += 1
      }
    })
    this.store.$.loadingState = null
    if (index !== 1) {
      name = name + index
    }
    object.name = name
    return object
  }

  saveObjects () {
    let objects = this.$parent.engine.objects
    objects.forEach(object => {
      let objectCode = ''
      objectCode += 'let ' + object.name + ' = new Object(engine)\n'
      objectCode += ' ' + object.name + '.setPosition(' + object.position[0] + ', ' +
                                                          object.position[1] + ', ' +
                                                          object.position[2] + ')\n'
      objectCode += ' ' + object.name + '.setRotation(' + object.rotation[0] + ', ' +
                                                          object.rotation[1] + ', ' +
                                                          object.rotation[2] + ')\n'
      objectCode += ' ' + object.name + '.scale(' + object.scaling[0] + ', ' +
                                                    object.scaling[1] + ', ' +
                                                    object.scaling[2] + ')\n'
      if (object.texture !== this.$parent.engine.noTexture) {
        objectCode += ' ' + object.name + '.setTexture(new Texture(' + object.texture.src + '))\n'
      }
      objectCode += ' ' + object.name + '.loadFromObj(' + object.filePath + ')\n'
      alert(objectCode)
    })
  }
}

export default ObjectManager



