<template>
  <ul class="menu" v-if="$s.engine">
    <li>
      <label>File</label>
      <ul>
        <label>Project</label>
        <li>Save</li>
        <li>Load project</li>
        <label>Export</label>
        <li>Export as JS</li>
        <li>Export as JSON</li>
      </ul>
    </li>

    <li>
      <label>View</label>
      <ul>
        <label>Debug</label>
        <li @click="$s.$.doShowDebug = !$s.$.doShowDebug">{{ $s.$.doShowDebug? 'Hide debug info' : 'Show debug info' }}</li>
      </ul>
    </li>
    
    <li>
      <label>Objects</label>
      <ul>
        <label>New Object</label>
        <li>
          <input
            class="hidden"
            type="file"
            id="files"
            name="files[]"
            multiple
            ref="files"
            accept=".obj"
            v-on:change="this.objectLoading"
          />Load from file
        </li>
        <li v-on:click="loadExampleObject">Load example</li>
        <label>Simple geometry</label>
        <li @click="objectsManager.createRect()">New rect</li>
        <li @click="objectsManager.createSphere()">New shere</li>
        <li @click="objectsManager.createGrid()">New infinity grid</li>
        <li @click="objectsManager.createSkybox()">New skybox</li>
      </ul>
    </li>

    <li>
      <label>Light</label>
      <ul>
        <label>Create</label>
        <li @click="$s.lightsManager.addLight()">New light</li>
      </ul>
    </li>

    <li>
      <label>Camera</label>
      <ul>
        <label>Set camera</label>
        <li>
          Set default camera
          <span class="arrow">></span>
          <ul>
            <li v-on:click="setCameraMode('fps')">FPS camera</li>
            <li v-on:click="setCameraMode('moba')">MOBA camera</li>
            <li v-on:click="setCameraMode('top')">Top view camera</li>
          </ul>
        </li>
        <li>Load camera from JSON</li>
        <li>Create camera with JS</li>
        <label>Settings</label>
        <li
          @click="camera.collisions = !camera.collisions"
        >
          {{ camera.collisions ? 'Disable colisions': 'Enable collisions' }}
        </li>
        <label>Controls</label>
        <li>Change control function</li>
        <li>
          Set default contorls
          <span class="arrow">></span>
          <ul>
            <li>Shooter controls</li>
            <li>MOBA controls</li>
            <li>Platformers controls</li>
          </ul>
        </li>
      </ul>
    </li>
    <li>
      <label>Info</label>
      <ul>
        <label>Help</label>
        <li>About</li>
        <li>Help</li>
        <li>Contact</li>
        <label>Info</label>
        <li class="">
          <p>Editor version</p>
          <span class="right">0.0.001.3 dev</span>
        </li>
        <li>
          <p>Engine version</p>
          <span class="right">0.0.002.51 dev</span>
        </li>
        <label>Update</label>
        <li>Update Engine</li>
        <li>Update Editor</li>
      </ul>
    </li>
  </ul>
</template>

<script>
import * as Bronze from "@m0ksem/bronze";
import $s from "./store";

export default {
  name: "Menu",
  data() {
    return {
      engine: this.$parent.engine
    };
  },
  computed: {
    $s: () => $s,
    objectsManager: () => $s.objectsManager,
    camera() { return $s.engine && $s.engine.camera },
  },
  methods: {
    setCameraMode(mode) {
      let camera = this.$parent.engine.camera;
      switch (mode) {
        case "fps":
          camera.setRotation(0, 0, 0);
          camera.setPosition(0, 800, 1500);
          break;
        case "moba":
          camera.setRotation(-45, 0, 0);
          camera.setPosition(0, 2000, 750);
          break;
        case "top":
          camera.setRotation(-90, 0, 0);
          camera.setPosition(0, 2500, 0);
          break;
      }
    },
    addObjectFromFile(objectText, filePath, updatedCb) {
      this.$s.objectsManager.addObject(objectText, filePath, updatedCb);
    },
    loadExampleObject() {
      this.$s.objectsManager.addKExample();
    },
    objectLoading(event) {
      let file = event.target.files[0];
      let CHUNK_SIZE = file.size / 8;
      if (CHUNK_SIZE > 1024 * 20) CHUNK_SIZE = 1024 * 20;
      let offset = 0;
      let fr = new FileReader();
      let text = "";
      fr.onload = () => {
        text += fr.result;
        offset += CHUNK_SIZE;
        seek(text);
      };

      let vue = this;
      let objLoaded = text => {
        $s.$.loadingState = 0.5
        vue.addObjectFromFile(text, file.name, (state) => {
          $s.$.loadingState = 0.5 + state / 2
        });
        vue.$data.selectedObjectName = file.name;
        vue.$data.fileText = text;
        $s.$.loadingState = null
      };

      let seek = text => {
        $s.$.loadingState = offset / file.size / 2
        if (offset >= file.size) {
          objLoaded(text);
          return;
        }
        var slice = file.slice(offset, offset + CHUNK_SIZE);
        fr.readAsText(slice);
      };

      seek(text);
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/vars.scss";
 .menu {
  user-select: none;
  list-style: none;
  padding: 0;
  margin: 0;
  // background: rgba($menu-background, 0.5);
  font-size: 0;
  padding: 0 18px;
  position: relative;
  z-index: 9999999;
  p {
    display: table-cell;
    margin: 0;
    padding: 0;
    padding-right: 5px;
    white-space: nowrap;
  }
  .right {
    display: table-cell;
    width: 100%;
    text-align: right;
  }
  input.hidden {
    position: absolute;
    opacity: 0;
  }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    filter: blur(1px);
    background: rgba($menu-background, 0.8);
  }
  & * {
    font-size: 14px;
    white-space: normal;
  }
  ul {
    padding: none;
    padding-left: 0px;
    z-index: 9999999999999;
    label {
      padding: 0px 12px;
    }
  }
  & > {
    li:hover:before {
      content: '';
      position: absolute;
      bottom: 5px;
      border-bottom: 2px solid rgba(white, 0.5);
      width: 80%;
      left: 50%;
      transform: translateX(-50%);
    }
    li:after {
      display: none;
    }
  }
  li {
    padding: 2px 16px;
    position: relative;
    list-style: none;
    cursor: pointer;
    color: rgb(194, 194, 194);
    &:hover {
      color: rgb(255, 255, 255);
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        filter: blur(1px);
        background: rgba($menu-background, 1);
      }
      & > ul {
        display: table;
      }
    }
    & > ul {
      position: absolute;
      display: none;
      left: 0;
      top: 100%;
      width: max-content;
      li {
        ul {
          top: 0;
          left: 100%;
        }
      }
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        filter: blur(1px);
        background: rgba($menu-background, 0.8);
      }
    }
  }
  & > li {
    display: inline-block;
    vertical-align: top;
  }
}
</style>



