<template>
  <div class="editor">
    <div class="top">
      <Menu />
    </div>
    <div class="mid">
      <div class="main" ref="window"/>
      <RightMenu v-show="engine" />
      <div id="debug" v-show="$s.$.doShowDebug">

      </div>
    </div>
    <LoadingBar :state="$s.$.loadingState"/>
  </div>
</template>

<script>
import $s from '@/components/store'
import Menu from '@/components/Menu.vue'
import RightMenu from './right-menu'
import LoadingBar from '../loadingbar'

export default {
  name: 'Main',
  components: {
    Menu, RightMenu, LoadingBar
  },
  computed: {
    $s: () => $s,
    engine: () => $s.$.engine,
    selectedObject: () => $s.$.selectedObject,
  },
  mounted () {
    this.$s.setupEngine(this.$refs.window)
    this.$s.setupCamera()
    this.$s.setupControls()
    this.$s.$.engine.run()
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '@/assets/vars.scss';
  .editor {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
    overflow-y: hidden;
    .top {
      height: 28px;
      line-height: 28px;
      width: 80%;
      text-align: left;
      color: white;
      z-index: 10;
    }
    .mid {
      white-space: nowrap;
      font-size: 0;
      height: 100%;
      width: 100%;
      text-align: left;
      box-sizing: border-box;
      margin-top: -28px ;
      #debug {
        position: absolute;
        left: 0;
        top: 28px;
        font-size: 14px;
        z-index: 0;
        color: $debug-color-text;
      }
      .menu {
        position: relative;
        width: 20%;
        height: 100%;
        display: inline-block;
        vertical-align: top;
        font-size: 14px;
        z-index: 12;
      }
      .main {
        position: relative;
        width: 80%;
        height: 100%;
        display: inline-block;
        vertical-align: top;
        box-sizing: border-box;
        resize: horizontal;
        padding-top: 28px;
        canvas {
          width: 100%;
          height: 100%;
          background: #1e1e1d;
          box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
        }
      }
    }
  }
</style>



