<template>
  <div class="group" v-if="textures.length > 0">
    <label>Colors</label>
    <div class="property property__button">
      <div class="color" v-for="texture in textures" :key="texture.id">
        <div class="color-img" :style="{'background-color': `rgba(${texture.color.join(',')})`}">
          <input type="color" 
            value="#e66465"
            class="hidden"
            @change="colorSelected($event, texture)"
          >
        </div> 
        
        <button v-on:click="deleteTexture(texture)" class="button lighten delete">X</button>
      </div>
    </div>
  </div>
</template>

<script>
import $s from '@/components/store'

export default {
  computed: {
    $s: () => $s,
    textures: () => $s.$.engine.textures.filter((texture) => !texture.image),
  },

  methods: {
    deleteTexture(texture) {
      $s.$.engine.textures.splice($s.$.engine.textures.indexOf(texture), 1);
    },
    colorSelected(e, texture) {
      texture.setColor(e.target.value)
    }
  }
}
</script>

<style lang="scss" scoped>
 .group {
    border: 1px solid rgba(0, 0, 0, 0.1);
    label {
      color: rgba(255, 255, 255, 0.5);
      padding: 8px 8px 4px 8px;
      display: block;
    }
  }
  .property {
    white-space: normal;
    .color {
      width: 20%;
      position: relative;
      padding: 1px;
      box-sizing: border-box;
      .color-img {
        height: 36px;
        width: 100%;
        position: relative;
      }
      .delete {
        color: white;
        border: none;
        padding: 8px;
        cursor: pointer;
        width: 100%;
      }
      .hidden {
        position: absolute;
        height: 100%;
        width: 100%;
        opacity: 0;
      }
      display: inline-block;
    }
  }
</style>


