<template>
  <input :value="value" @input="onInput" class="input" type="text">
</template>

<script>
export default {
  props: ['value', 'type'],

  methods: {
    onInput(e) {
      let value = e.target.value;
      if (this.type === 'number') {
        if (value != '-' && Number(value) != value) return e.target.value = this.prevValue || 0;
        this.prevValue = value;
        this.$emit('input', value)
      }
    }
  }
}
</script>


