<template>
  <div id="app">
    <div class="wrapper">
      <router-view/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style lang="scss">
@import '@/assets/vars.scss';
#app {
  font-family: 'Hammersmith One', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #111110;
  background: #161616;
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0%;
  top: 0%
}

.wrapper {
  position: relative;
  height: 100%;
  max-width: 100%;
  max-height: 100%
}

label {
  color: white;
}

button, .button {
  border: rgba(0, 0, 0, 0.1);
  color: white;
  cursor: pointer;
  background: linear-gradient(90deg, $card-background, $card-background);
  padding: 8px;
  box-sizing: border-box;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  cursor: pointer;
  text-align: center;
  color: white;

  &:hover {
    background: linear-gradient(90deg, lighten($card-background, 8%), lighten($card-background, 8%));
  }
  &.darken {
    background: rgba($color: #000000, $alpha: 0.2);
    &:hover {
      background: linear-gradient(90deg, lighten($card-background, 5%), lighten($card-background, 5%));
    }
  }
  &.lighten {
    &:hover {
      background: linear-gradient(90deg, lighten($card-background, 8%), lighten($card-background, 8%));
    }
  }
}

.input {
  padding: 8px;
}

input.input, select {
  background: rgba(0, 0, 0, 0.2);
  border: none;
  color: white;
  &:hover {
    background: rgba(255, 255, 255, .05);
  }
  &.lighten {
    background: linear-gradient(90deg, lighten($card-background, 5%), lighten($card-background, 5%));
    color: white;
    &:hover {
      background: linear-gradient(90deg, lighten($card-background, 8%), lighten($card-background, 8%));
    }
  }
}
// SELECT
select {
  padding: 4px;
  option {
    color: black;
    background: linear-gradient(90deg, lighten($card-background, 8%), lighten($card-background, 8%));
  }
}


// POPUP
.popup-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 99;
  .bg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.5);
  }
}

.popup {
  z-index: 999;
  position: relative;
  top: 50%;
  left: 50%;
  width: 70%;
  max-height: 80%;
  transform: translateX(-50%) translateY(-50%);
  box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14),
    0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  background-attachment: fixed;
  z-index: 9999;
  overflow-y: auto;
  background: rgba($popup-background, 0.9);
  .header {
    background: linear-gradient(
      90deg,
      rgba($menu-background, 1),
      rgba($menu-background, 1)
    );
    position: relative;
    color: white;
    font-size: 16px;
    border-bottom: 1px solid rgba($menu-background, 0.3);
    box-shadow: -1px 10px 50px -49px rgba(0, 0, 0, 0.75);
    z-index: 999;
    height: 32px;
    line-height: 34px;
    text-align: left;
    .close {
      position: absolute;
      right: 0;
      top: 50%;
      background: transparent;
      border: none;
      color: white;
      width: 44px;
      height: 100%;
      cursor: pointer;
      transform: translateY(-50%);
      &:hover {
        background: rgba(226, 76, 76, 1);
      }
    }
    label {
      display: inline-block;
      padding: 0px 16px;
    }
  }
}

</style>



