<template>
  <div class="popup-wrapper" @mousemove="$event.stopPropagation()" v-if="doShow">
    <div class="popup">
      <div class="header">
        <label class="group_name">Texture settings</label>
        <button class="close" @click="doShow = false">X</button>
      </div>

      <div class="content">
        <div class="wrapper">
          <div class="image-container">
            <div v-if="texture" class="big-image" v-html="texture.image && texture.image.outerHTML" />
            <div class="inner" />
          </div>
        </div>
        <div class="wrapper props">
          <div class="prop">
            <label>Texture type</label>
            <select v-model="textureType" >
              <option>SimpleTexture</option>
              <option>CubeTexture</option>
              <option>ReflectionTexture</option>
              <option>ColorTexture</option>
            </select>
          </div>

           <div class="prop">
            <label class="name">Alpha</label>
            <input type="checkbox" v-model="texture.alpha"/>
          </div>

          <div class="prop">
            <label class="name">Mipmap filter</label>
            <select @change="onMipmapSelect" :value="texture.mipmapFilter">
              <option>NEAREST</option>
              <option>LINEAR</option>
              <option>NEAREST_MIPMAP_NEAREST</option>
              <option>LINEAR_MIPMAP_NEAREST</option>
              <option>LINEAR_MIPMAP_NEAREST</option>
              <option>LINEAR_MIPMAP_LINEAR</option>
            </select>
          </div>

          <div class="prop">
            <label class="name">Mipmap generation method</label>
            <button class="button darken"
            :class="{'selected': texture._mipmapGenerationMethod == 0 }"
            @click="setMipmapGenerationMethod(0)"
            >
              Auto generate
            </button>
            <button class="button darken" 
            :class="{'selected': texture._mipmapGenerationMethod == 1 }"
            @click="setMipmapGenerationMethod(1)"
            >
              Quick generate
            </button>
          </div>
          <div class="prop">
            <label class="name">Sizes</label><br/>
            <label for="autogenerate">Height</label>
            <input class="input lighten" type="number" v-model="texture.width" name="autogenerate" >
            <label for="quickgenerate">Width</label>
            <input class="input lighten" type="number" v-model="texture.height" name="quickgenerate" >
          </div>
        </div>
      </div>
    </div>
    <div class="bg" @click="doShow = false" />
  </div>
</template>

<script>
import $s from "@/components/store";

export default {
   data() {
    return { doShow: false, texture: null, textureType: '' };
  },
  computed: {
    $s: () => $s,
    textures: () => $s.$.engine.textures,
    colorTextures: () => $s.$.engine.textures.filter(texture => !texture.image),
    imageTextures: () => $s.$.engine.textures.filter(texture => texture.image),
    manager: () => $s.texturesManager
  },

  methods: {
    show(texture) {
      this.texture = texture;
      this.doShow = true;
      this.textureType = texture.constructor.name;
    },
    setMipmapGenerationMethod(method) {
      this.texture.setMipmapGenerationMethod(method)
      this.texture._createWebglTexture()
    },
    onMipmapSelect(e) {
      const value = e.target.value;
      this.texture.mipmapFilter = value;
      console.log(value)
      this.texture._createWebglTexture();
    }
  },

  watch: {
    textureType(val) {
      // this.manager.removeTexture();
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/vars.scss";
.popup-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  // z-index: 99;
  cursor: default;
  .bg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.5);
    cursor: default;
  }
}

.content {
  padding: 16px;
  box-sizing: border-box;
  white-space: nowrap;
  & > .wrapper {
    padding: 8px;
    display: inline-block;
    box-sizing: border-box;
    vertical-align: top;
    width: 50%;
    & > * {
      border-radius: 5px;
      background: $card-background;
    }
  }
  .image-container {
    position: relative;
    display: inline-block;
    width: 100%;
    .inner {
      margin-top: 100%;
    }
    .big-image {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }
  }

  label {
    color: white;
    padding: 8px;
    display: inline-block;
  }

  .props {
    height: 100%;
    overflow-y: auto;
    & > * {
      margin-bottom: 8px;
      padding: 8px;
    }
    .button {
      display: inline-block;
      width: auto;
      border-radius: 5px;
      &.selected {
        background: rgba($primary, 1);
      }
    }
  }
}
</style>

<style lang="scss">
  .big-image {
    img {
      top: 50%;
      left: 50%;
      position: relative;
      transform: translateX(-50%) translateY(-50%);
      max-height: 100%;
      max-width: 100%;
    }
  }
</style>



