<template>
  <div class="menu" v-if="engine">
    <SwitchButtns v-model="selectedMenu" />
    <Objects v-if="selectedMenu === 'objects'" />
    <Textures v-if="selectedMenu === 'textures'" />
    <!-- <Lights v-if="selectedMenu === 'lights'"/> -->
  </div>
</template>

<script>
import Objects from "./objects";
import Textures from "./textures";
import $s from "@/components/store";
import SwitchButtns from './switch-buttons';

export default {
  components: { Objects, Textures, SwitchButtns },

  data() {
    return { doShowObjects: true, selectedMenu: 'objects' };
  },
  computed: {
    $s: () => $s,
    engine: () => $s.$.engine,
    objects: () => $s.$.engine.objects
  },
};
</script>

<style lang="scss" scoped>
 @import '@/assets/vars.scss';
.menu {
  text-align: left;
  box-sizing: border-box;
  background: $right-menu-background;
  overflow: hidden;
  box-shadow: 0 8px 17px 2px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.2);
}
</style>


