<template>
  <div class="objects-wrapper" v-if="engine">
    <div class="objects">
      <label class="no-objects" v-if="objects.length == 0">There are no objects now</label>
      <div v-else-if="doShowObjects">
        <template v-for="object in objects"
        >
          <ObjectComponent  :key="object.id" :object="object" />
        </template>
      </div>
    </div>
    <div class="props-wrapper">
      <label>Properties</label>
      <div class="props">
        
        <ObjectProps v-if="selectedObject && selectedObject.type == 'object'" 
          class="object-props"
          :object="selectedObject"
          @name-changed="rerenderObjects"
        />
        <LightProps v-if="selectedObject && selectedObject.type == 'light'" 
          class="object-props"
          :light="selectedObject.light"
        />
      </div>
    </div>
  </div>
</template>

<script>
import ObjectProps from './props/object'
import ObjectComponent from './objects/object'
import LightProps from './props/light'
import $s from '@/components/store'

export default {
  components: { ObjectProps, ObjectComponent, LightProps },

  data() {
    return { doShowObjects: true }
  },
  computed: {
    $s: () => $s,
    engine: () => $s.$.engine,
    objects: () => $s.objectsManager.objects,
    lastObject: () => { return $s.objectsManager.objects[0] },
    engineHoveredObject: () => { return $s.$.engine.selectedObject },
    selectedObject: () => { return $s.$.selectedObject || this.lastObject },
  },

  methods: {
    rerenderObjects() {
      // this.doShowObjects = false;
      // this.$nextTick(() => this.doShowObjects = true)
    }
  },
}
</script>

<style lang="scss" scoped>  
  @import '@/assets/vars.scss';
  .objects-wrapper {
    height: 100vh;
    padding-top: 40px;
    margin-top: -40px;
    position: relative;
    box-sizing: border-box;
    .objects {
      height: 50%;
      overflow-y: scroll;
      box-sizing: border-box;
      padding: 8px;
    }
    .props-wrapper {
      height: 50%;       
      .props {
        overflow-y: scroll;
        height: 100%;
        margin-top: -35px;
        padding: 35px 8px 8px 8px;
        & > * {
          padding-bottom: 43px;
        }
      }
      label {
        display: block;
        padding: 8px;
        color: white;
        height: 35px;
        box-sizing: border-box;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        background: $right-menu-background;
        z-index: 10;
        position: relative;
      }
    }
    
  }

</style>


