<template>
<div class="popup-wrapper" v-if="doShow">
  <div class="popup">
    <div class="header">
      <label class="group_name">Select Texture</label>
      <button class="close" @click="doShow = false">X</button>
    </div>
  
    <loadButtons />

    <div class="mtl" v-if="object.mtl">
      <label class="head">Select MTL texture:</label>
      <div class="material" 
        v-for="mtl in object.mtl.elements" 
        :key="mtl.id"
        @click="selectMaterial(mtl)"
      >
        <label>{{ mtl.name }}</label>
        <div
          v-if="mtl.texture.constructor.name === 'ColorTexture'"
          class="color-texture texture"
        >
          <div class="color" 
            :style="{'background': `rgba(${mtl.texture.color.join(',')})`}"
            :class="{'selected': selectedMaterial === mtl }"
          />
        </div>
        <div
          v-if="mtl.texture.constructor.name === 'SimpleTexture'"
          class="image-texture texture"
          v-html="mtl.texture.image.outerHTML"
          :class="{'selected': selectedMaterial === mtl }"
        ></div>
      </div>
    </div>

    <div class="group">
      <label class="group_name">Color textures</label>
      <div
        class="color-texture texture"
        v-for="texture in colorTextures"
        :key="texture.id"
        @click="selectTexture(texture)"
      >
        <div class="color" 
          :style="{'background': `rgba(${texture.color.join(',')})`}"
          :class="{'selected': isSelected(texture) }"
        />
      </div>
    </div>

    <div class="group">
      <label class="group_name">Image textures</label>
      <div
        class="image-texture texture"
        v-for="texture in imageTextures"
        :key="texture.id"
        v-html="texture.image.outerHTML"
        :class="{'selected': isSelected(texture) }"
        @click="selectTexture(texture)"
      ></div>
    </div>
  </div>
  <div class="bg" @click="doShow = false"/>
</div>

</template>

<script>
import $s from "@/components/store";
import loadButtons from "../textures/load-buttons";

export default {
  components: { loadButtons },
  data() {
    return { doShow: false, object: null, selectedMaterial: null };
  },
  computed: {
    $s: () => $s,
    textures: () => $s.$.engine.textures,
    colorTextures: () => $s.$.engine.textures.filter(texture => !texture.image),
    imageTextures: () => $s.$.engine.textures.filter(texture => texture.image),
    manager: () => $s.texturesManager
  },

  methods: {
    show(object) {
      this.doShow = true;
      this.object = object;
    },
    selectTexture(texture) {
      if (this.selectedMaterial) {
        this.selectedMaterial.texture = texture;
      } else {
        this.object.setTexture(texture);
      }
    },
    selectMaterial(mtl) {
      this.selectedMaterial = mtl
    },
    isSelected(texture) {
      if (this.selectedMaterial) {
        return this.selectedMaterial.texture == texture;
      } else {
        return this.object.texture == texture
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "@/assets/vars.scss";

.popup {
  .group {
    padding: 0 16px;
    box-sizing: border-box;
    overflow-x: auto;
    label {
      color: rgba(255, 255, 255, 0.5);
      display: block;
      padding: 16px 0px 8px 0px;
    }
  }

  .color-texture {
    display: inline-block;
    .color {
      width: 48px;
      height: 48px;
      position: relative;
      cursor: pointer;
      border-radius: 5px;
      overflow: hidden;
      margin-right: 4px;
      &.selected {
        &::before {
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          text-align: center;
          font-size: 24px;
          color: rgba(46,206,113,1);
          text-shadow: 0 0 10px rgba(46,206,113,.5);
          background: rgba(#2d7d5e, 0.5);
          border: 4px solid rgba(#2d7d5e, 0.6);
          box-sizing: border-box;
        }
      }
      &:hover {
        &::after {
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          background: rgba(255, 255, 255, 0.1);
        }
      }
    }
  }

  .mtl {
    padding: 8px;
    overflow-y: auto;
    .head {
      margin-bottom: 20px;
    }
    label {
      color: white;
      display: block;
    }
    .material {
      display: inline-block;
      margin-right: 8px;
      vertical-align: top;
      .texture {
        display: block;
        width: 100%;
        .color {
          width: 100%;
        }
      }
    }
  }
}
</style>

<style lang="scss">
@import "@/assets/vars.scss";
.texture.selected {
  &::before {
    content: "";
    position: absolute;
    text-align: center;
    font-size: 24px;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
    vertical-align: middle;
    z-index: 10;
    color: rgba(46,206,113,1);
    text-shadow: 0 0 10px rgba(46,206,113,.5);
  }
  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    text-align: center;
    font-size: 24px;
    background: rgba(#2d7d5e, 0.5);
    border: 4px solid rgba(#2d7d5e, 0.6);
    border-radius: 5px;
    box-sizing: border-box;
    line-height: 100%;
    vertical-align: middle;
  }
}

.image-texture {
  display: inline-block;
  position: relative;
  margin-right: 16px;
  max-height: 256px;
  max-width: 256px;
  cursor: pointer;
  vertical-align: top;
  border-radius: 5px;
  overflow: hidden;
  background: $card-background;
  &::after {
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    background: rgba(255, 255, 255, 0.1);
  }
  &:hover {
    &::after {
      content: '';
    }
  }
  img {
    max-height: 100%;
    max-width: 100%;
  }
}
</style>



