<template>
  <div class="group simple-texture" v-if="textures.length > 0">
    <label>Images</label>
    <div class="property property__button image" 
      v-for="texture in textures" 
      :key="texture.id"  
      @click="openSettings(texture)">
      <div class="image-wrapper" v-html="texture.image.outerHTML" />
      <button v-on:click="deleteTexture(texture)" class="button lighten delete">X</button>
    </div>
    <TextureSettings ref="settings"/>
  </div>
</template>

<script>
import $s from '@/components/store'
import TextureSettings from './texture-settings'

export default {
  components: { TextureSettings },

  computed: {
    $s: () => $s,
    textures: () => $s.$.engine.textures.filter((texture) => texture.image),
  },

  methods: {
    deleteTexture(texture) {
      $s.$.engine.textures.splice($s.$.engine.textures.indexOf(texture), 1);
    },
    openSettings(texture) {
      this.$refs.settings.show(texture)
    }
  }
}
</script>

<style lang="scss" scoped>
  .simple-texture {
    cursor: pointer;
  }
 .group {
    label {
      color: rgba(255, 255, 255, 0.5);
      padding: 8px 8px 4px 8px;
      display: block;
    }
  }
  .property {
    white-space: normal;
    &.image {
      margin-bottom: 8px;
      .delete {
        width: 100%;
        color: white;
        border: none;
        padding: 8px;
        cursor: pointer;
      }
    }
  }
</style>

<style lang="scss">
  .textures {
    .image-wrapper {
      display: block;
      text-align: center;
      height: 124px;
      max-width: 100%;
      overflow: hidden;
      position: relative;
      img {
        position: relative;
        top: 50%;
        max-width: 100%;
        transform: translateY(-50%);
      }
    }
  }
</style>


