<template>
<div class="popup-wrapper" v-if="state">
  <div class="popup loading">
    <div class="header">
      <label class="group_name">Loading</label>
    </div>
    <div class="bar">
      <div class="fill" :style="{'width': state * 100+ '%'}">
        <div class="color"/>
      </div>
    </div>
  </div>
  <div class="bg"/>
</div>

</template>

<script>
export default {
  props: ['state']
}
</script>

<style lang="scss" scoped>
@import '@/assets/vars.scss';
 .popup.loading {
    // padding: 8px;
    min-height: 64px;
  }
  .bar {
    width: 100%;
    height: 32px;
    position: relative;
    .fill {
      // background: red;
      // position: relative;
      width: 10%;
      height: 100%;
      overflow: hidden;
      .color {
        // position: absolute;
        width: 100%;
        height: 100%;
        background: $primary;
        z-index: 0;
      }
    }
  }
</style>


