import Vue from "vue";
import * as Bronze from "@m0ksem/bronze";
import texturesManager from "./texture.manager";
import ObjectsManager from "./objects.manager";
import LightManager from "./light.manager";
import VertexShader from './shaders/default/vertex-shader.glsl'
import FragmentShader from './shaders/default/fragment-shader.glsl'

class Store {
  constructor() {
    this.$ = Vue.observable({
      engine: null,
      selectedObject: null,
      selectedObjectName: "",
      doShowDebug: false,
      loadingState: null
    });

    this.texturesManager = null;
    this.objectsManager = null;
  }

  get engine() {
    return this.$.engine;
  }
  set engine(val) {
    this.$.engine = val;
  }

  setupEngine(div) {
    this.$.engine = new Bronze.Engine(div);
    const engine = this.$.engine;
    window.bronze = engine

    this.$.engine.shaders.addProgram("test", VertexShader, FragmentShader,  {
      removePrefixes: true,
      addLocationMarker: true
    });
    this.engine.shaders.shadersRequireLights.push(this.engine.shaders.test);

    this.texturesManager = new texturesManager(this.$.engine, this);
    this.objectsManager = new ObjectsManager(this.$.engine, this);
    this.lightsManager = new LightManager(this.$.engine, this)

    var debug = new Bronze.Debugger(engine);
    debug.setElement(document.getElementById("debug"));
    debug.addLog(debug.createLogView(), () => {
      return "Engine status " + engine.status;
    });
    debug.addLog(debug.createLogView(), () => {
      return ("Mouse position " + engine.controls.mouse.x + " "  + engine.controls.mouse.y);
    });
    debug.addLog(debug.createLogView(), () => {
      return ("Mouse movement " + engine.controls.mouse.movement.x + " " + engine.controls.mouse.movement.y);
    });
    debug.addLog(debug.createLogView(), () => {
      if (engine.selectedObject) {
        return "Hover on [ " + engine.selectedObject.name + " ] object";
      } else {
        return "No objects selected";
      }
    });
  }

  setupCamera() {
    let camera = new Bronze.Camera(this.$.engine);
    camera.setPosition(0, 800, 1200);
    camera.setFieldOfView(90);
    camera.setCubeCollisionBox(100);
    camera.setRotation(0, 0, 0);
    this.$.engine.setCamera(camera);
    this.$.engine.setDrawingRange(100000000000000);
  }

  setupControls() {
    let controls = new Bronze.Controls(this.$.engine);
    let engine = this.$.engine;
    let camera = this.$.engine.camera;

    engine.globalLightMinValue = 0.01;
    
    controls.lockPointer(false);

    engine.div.addEventListener('dblclick', () => {
      if (engine.selectedObject != null) {
        this.$.selectedObject = engine.selectedObject;
      } else {
        this.$.selectedObject = null;
      }
    })

    let toCameraRange = null

    controls.setControlFunction(() => {
      if (controls.keys[87]) {
        if (controls.keys[16]) {
          camera.move(0, 10, 0);
        } else {
          camera.move(
            camera.inverseRotationMatrix[2] * -10,
            0,
            camera.inverseRotationMatrix[10] * -10
          );
        }
      }
      if (controls.keys[83]) {
        if (controls.keys[16]) {
          camera.move(0, -10, 0);
        } else {
          camera.move(
            camera.inverseRotationMatrix[2] * 10,
            0,
            camera.inverseRotationMatrix[10] * 10
          );
        }
      }
      if (controls.keys[65]) {
        camera.move(
          camera.inverseRotationMatrix[0] * -10,
          camera.inverseRotationMatrix[4] * -10,
          camera.inverseRotationMatrix[8] * -10
        );
      }
      if (controls.keys[68]) {
        camera.move(
          camera.inverseRotationMatrix[0] * 10,
          camera.inverseRotationMatrix[4] * 10,
          camera.inverseRotationMatrix[8] * 10
        );
      }

      if (
        controls.mouse.buttons[2] ||
        controls.touch.actionBeforeMove == "long click"
      ) {
        if (this.$.selectedObject) {
          const object = this.$.selectedObject;
          if (!controls.keys[17]) {
            toCameraRange = toCameraRange || Bronze.Vector3.distance(camera.position, object.position);
            object.moveRelativeToTheCamera(
              controls.mouse.x,
              controls.mouse.y,
              toCameraRange
            );
          } else {
            object.moveRelativeToTheCamera(0, -controls.mouse.movement.y, 0);
          }
        }
      } else {
        toCameraRange = null
      }

      if (
        controls.mouse.buttons[0] ||
        controls.pointerLocked ||
        controls.touch.actionBeforeMove == "click"
      ) {
        if (controls.keys[17]) {
          camera.rotate(0, 0, controls.mouse.movement.y / 10);
        } else {
          camera.rotate(
            controls.mouse.movement.y / 10,
            controls.mouse.movement.x / 10,
            0
          );
        }
      }
    });
  }
}

export default new Store();



