<template>
  <div class="switch-buttons">
    <button :class="{'selected': value === 'objects'}" @click="select('objects')">Objects</button>
    <button
      :class="{'selected': value === 'textures'}"
      @click="select('textures')"
    >Textures</button>
  </div>
</template>

<script>
export default {
  // data: () => ({ value: 'objects' }),

  props: ['value'],

  methods: {
    select(value) {
      this.$emit('input', value)
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/vars.scss';

  .switch-buttons {
    font-size: 0;
    // background: linear-gradient(90deg, #3a86ff 0%, #8338ec 100%);
    position: relative;
    & > * {
      color: white;
      background: lighten($right-menu-background, 5%);
      border: rgba(0, 0, 0, 0.1);
      width: 50%;
      height: 40px;
      position: relative;
      z-index: 1;
      cursor: pointer;
      user-select: none;
      outline: none;
      padding: 0;
      border: 3px solid rgba(0, 0, 0, 0.1);
      font-weight: bold;
      &:hover {
        background: lighten($right-menu-background, 15%);
      }
      &:after {
        content: '';
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
        z-index: -1;
      }
      &.selected {
        &::after {
          background: $buttons-background;
        }
      }
    }
  }
</style>


