<template>
  <div class="object"
    :class="{'engine-hover': engineHoveredObject == object, 
    'selected': selectedObject == object }"
  >
    <div class="name" @click="selectObject(object)"><span>{{ object.name }}</span></div>
    <button v-on:click="deleteObject(object)" class="delete">X</button>
  </div>
</template>

<script>
import $s from '@/components/store'

export default {
  props: ['object'],

  computed: {
    $s: () => $s,
    engine: () => $s.$.engine,
    objects: () => $s.objectsManager.objects,
    engineHoveredObject: () => { return $s.$.engine.selectedObject },
    selectedObject: () => { return $s.$.selectedObject || this.lastObject },
  },

  methods: {
    selectObject () {
      this.$s.$.selectedObject = this.object
    },
    deleteObject (object, e) {
      if (object.type === 'object') {
        let objectIndex = this.engine.removeObject(object)

        if (this.engine.objects.length < 1) {
          this.$s.$.selectedObject = null;
          return;
        }
        this.$s.$.selectedObject = this.engine.objects[this.engine.objects.length - 1];
      } else if (object.type == 'light') {
        this.$s.lightsManager.removeLight(object.light)
      }
    },
  },
}
</script>

<style lang="scss" scoped>
@import '@/assets/vars.scss';
  .object {
    background: $card-background;
    height: 40px;
    display: table;
    margin-top: 8px;
    & > * {
      display: table-cell;
      vertical-align: middle;
    }
    &.engine-hover {
      .name {
        background: $hover-object;
      }
    }
    &.selected {
      background: $selected-object;
    }
    .name {
      color: white;
      width: 100%;
      padding: 0 16px;
      box-sizing: border-box;
      cursor: pointer;
      &:hover {
        background: $hover-object;
      }
    }
    .delete {
      height: 40px;
      width: 40px;
      float: right;
      &:hover {
        background: $delete-object;
      }
    }
  }

</style>


