<script>
import SelectTexturePopup from '../select-texture-popup';
import InputBox from './input'
import { MTL } from '@m0ksem/bronze'
import $s from '@/components/store'

export default {
  props: ['object'],
  components: { SelectTexturePopup, InputBox },

  methods: {
    showTexturePopup() {
      this.$refs['texture-popup'].show(this.object);
    },
    useObjectInConsole() {
      console.log(this.object)
      window[this.object.name] = this.object
    },
    loadTML(e) {
      let file = e.target.files[0];
      let CHUNK_SIZE = file.size / 8;
      if (CHUNK_SIZE > 1024 * 20) CHUNK_SIZE = 1024 * 20;
      let offset = 0;
      let fr = new FileReader();
      let text = "";
      fr.onload = () => {
        text += fr.result;
        offset += CHUNK_SIZE;
        $s.$.loadingState = offset / file.size;
        seek(text);
      };

      $s.$.loadingState = 0
      let seek = text => {
        if (offset >= file.size) {
          let mtl = new MTL(text, $s.engine, '/')
          this.object.mtl = mtl;
          this.object.compile(null, (state) => {
            $s.$.loadingState = 0.5 + state / 2
          })
          $s.$.loadingState = null
          return;
        }
        var slice = file.slice(offset, offset + CHUNK_SIZE);
        fr.readAsText(slice);
      };

      seek(text);
    }
  }
}
</script>

<template>
  <div class="object-props" v-if="object">
    <label class="title">Object properties</label>
    <table>
      <tr class="property">
        <td class="name">
          Name
        </td>
        <td class="value">
          <input class="input" type="text" v-model="object.name" @input="$emit('name-changed')" />
        </td>
      </tr>

      <tr class="property">
        <td class="name big">Position</td>
        <td class="value">
          <div class="row">
            <label>X</label>
            <InputBox type="number" v-model="object.position.x" />
          </div>
          <div class="row">
            <label>Y</label>
            <InputBox type="number" v-model="object.position.y" />
          </div>
          <div class="row">
            <label>Z</label>
            <InputBox type="number" v-model="object.position.z" />
          </div>
        </td>
      </tr>

      <tr class="property">
        <td class="name big">Scale</td>
        <td class="value">
          <div class="row">
            <label>X</label>
            <input class="input" type="text" v-model="object.scaling.x" />
          </div>
          <div class="row">
            <label>Y</label>
            <input class="input" type="text" v-model="object.scaling.y" />
          </div>
          <div class="row">
            <label>Z</label>
            <input class="input" type="text" v-model="object.scaling.z" />
          </div>
        </td>
      </tr>

      <tr class="property">
        <td class="name big">Rotation</td>
        <td class="value">
          <div class="row">
            <label>X</label>
            <div class="unit-input" data-unit="deg">
              <input class="input" type="text" v-model.lazy="object.rotation.x"/>
            </div>
          </div>
          <div class="row">
            <label>Y</label>
            <div class="unit-input" data-unit="deg">
              <input class="input" type="text" v-model.lazy="object.rotation.y"/>
            </div>
          </div>
          <div class="row">
            <label>Z</label>
            <div class="unit-input" data-unit="deg">
              <input class="input" type="text" v-model.lazy="object.rotation.z"/>
            </div>
          </div>
        </td>
      </tr>

      <tr class="property">
        <td class="name big">Texture</td>
        <td class="value">
          <button @click="showTexturePopup" class="button darken">Select Texture</button>
          <SelectTexturePopup ref="texture-popup"/>
        </td>
      </tr>

      <tr class="property">
        <td class="name big">MTL</td>
        <td class="value">
          <input
            class="button"
            type="file"
            id="files"
            name="files[]"
            multiple
            ref="files"
            accept=".mtl"
            @change="loadTML"
          />
        </td>
      </tr>

      <tr class="property">
        <td colspan="2">
          <button @click="useObjectInConsole" class="button darken">Use in console</button>
        </td>
      </tr>
    </table>
  </div>
</template>

<style lang="scss" scoped>
  @import '@/assets/vars.scss';
  .object-props {
    .title {
      color: white;
      text-align: center;
    }
    table {
      width: 100%;
      border-collapse: separate;
      border-spacing:0 8px; 
    }
    .property {
      background: $card-background;
      margin-top: 8px;
      padding: 8px;
      width: 100%;
      .name {
        color: white;
        padding: 8px;
        &.big {
          padding-top: 16px;
          vertical-align: top;
        }
      }
      .value {
        display: table;
        width: 100%;
        padding-right: 8px;
        box-sizing: border-box;
        .unit-input {
          &[data-unit] {
            display: table;
            height: auto;
            border-collapse: separate;
            border-spacing: 0 0px;
            &::after {
              content: attr(data-unit);
              background: rgba($color: #000000, $alpha: 0.2);
              display: table-cell;
              padding: 0 8px;
              color: rgba($color: #fff, $alpha: 0.2)
            }
          }
        }
        input {
          width: 100%;
          padding: 8px 12px;
          border: none;
          width: 100%;
          box-sizing: border-box;
          color: white;
        }
        .row {
          display: table-row;
          & > * {
            display: table-cell;
          }
          label {
            color: white;
            width: 24px;
            height: 24px;
            line-height: 24px;
            text-align: center;
            background: rgba($color: #000000, $alpha: 0.4);
          }
          .input {
            position: relative;
            .unit {
              position: absolute;
              right: 0;
              top: 50%;
              transform: translateY(-50%);
              display: table-cell;
              color: rgba($color: #FFF, $alpha: .5);
            }
          }
        }
      }
      button {
        padding: 8px;
        width: 100%
      }
    }
  }
</style>


